//引入接口定义的js文件
import operateApi from "@/api/operate";
import touristApi from "@/api/tourist";
import { getToken } from "@/utils/auth";
export default {
  //定义变量和初始值
  data() {
    return {
      dialogVisible: false,
      current: 1, //当前页
      limit: 10, //每页显示记录数
      searchObj: {}, //条件封装对象
      contentList: [], //每页数据集合
      total: 0, //总记录数
      multipleSelection: [], // 批量选择中选择的记录列表
      cid: {},
      selectAid: "",
      messagessd: "",
    };
  },
  created() {
    if (getToken() === undefined) {
      this.$router.push({ path: "/userlogin", query: { id: "1" } });
    } else {
      //在页面渲染之前执行
      //一般调用methods定义的方法，得到数据
      this.getList();
    }
  },
  mounted() {
    this.messagessd = this.$route.query.types;
    if (this.messagessd == "1") {
      this.open1();
    }
    //定义方法，进行请求接口调用
  },

  methods: {
    handleStatus(status) {
      switch (status) {
        case 0:
          return "审核中";
        case 1:
          return "正常";
        case 2:
          return "下架";
        case 3:
          return "审核不通过";
        default:
          return "审核中";
      }
    },
    lookByCid(value) {
      this.$router.push({ path: "/c/" + value });
    },
    //消息提示
    open1() {
      const h = this.$createElement;
      this.$notify({
        title: "提交成功!",
        message: h(
          "i",
          { style: "color: teal" },
          "请耐心等待管理员审核，审核通过后即可在首页显示！"
        ),
      });
    },
    //删除方法
    deleteByCid() {
      //确定执行then方法
      //调用接口
      operateApi.deleteContent(this.selectAid).then((response) => {
        //提示
        // this.$message({
        //   type: "success",
        //   message: "删除成功!",
        // });
        this.$notify({
          title: "提示：",
          message: this.$createElement(
            "i",
            { style: "color: red" },
            "删除成功!"
          ),
        });
        //刷新页面
        this.getList();
        //关闭弹出层
        this.dialogVisible = false;
      });
    },
    //弹出层
    dialogOpen(id) {
      this.dialogVisible = true;
      this.selectAid = id;
    },
    //获取选择复选框的id值
    handleSelectionChange(selection) {
      this.multipleSelection = selection;
      // console.log(selection);
    },

    //用户设置列表
    getList(page = 1) {
      //添加当前页参数
      this.current = page;
      operateApi
        .ownContents(this.searchObj)
        .then((response) => {
          //请求成功response是接口返回数据
          //返回集合赋值list
          this.contentList = response.rows;
          //总记录数
          this.total = parseInt(response.total);
          // console.log(this.list);
        })
        .catch((error) => {
          //请求失败
          //console.log("失败" + error);
        });
    },
  },
};
